import React from 'react';
import PropTypes from 'prop-types';
import PhoneItem from './PhoneItem';

const Phone = ({ users, _getPhoneItem, _getCardArr }) => {
  return (
    <section id='smartphone' className='container-fluid pt-5 pb-5'>
      <h1 className='text-white text-center'>BEST SMARTPHONE</h1>
      <div className='row'>
        {users.map(user => (
          <PhoneItem
            key={user.id}
            phone={user}
            _getPhoneItem={_getPhoneItem}
            _getCardArr={_getCardArr}
          />
        ))}
      </div>
    </section>
  );
};

PhoneItem.propTypes = {
  _getPhoneItem: PropTypes.func.isRequired,
  _getCardArr: PropTypes.func.isRequired,
  users: PropTypes.array
};

export default Phone;
