import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import './App.css';

import Nav from './components/layout/Nav';
import Slide from './components/layout/Slide';
import Phone from './components/layout/Phone';
import Laptop from './components/layout/Laptop';
import Promo from './components/layout/Promo';
import Modal from './components/layout/Modal';
import smartPhoneArr from './components/data/PhoneData';
import laptopArr from './components/data/LaptopData';
import Card from './components/layout/Card';

const App = () => {
  const [phone, setPhone] = useState({});
  const [users, setUsers] = useState([]);
  const [laptops, setLaptops] = useState([]);
  const [cardArr, setCardArr] = useState([]);

  useEffect(() => {
    setUsers(smartPhoneArr);
    setLaptops(laptopArr);
    // eslint-disable-next-line
  }, []);

  const _getPhoneItem = phone => {
    setPhone(phone);
  };

  const _getCardArr = phone => {
    let index = cardArr.findIndex(item => item.id === phone.id);

    if (index === -1) {
      var cloneItemsInCart = [...cardArr];
      cloneItemsInCart.push(phone);
      setCardArr(cloneItemsInCart);
    }
  };

  return (
    <div className='App'>
      <Nav numb={cardArr.length} />
      <Slide />
      <Phone
        users={users}
        _getPhoneItem={_getPhoneItem}
        _getCardArr={_getCardArr}
      />
      <Laptop
        laptops={laptops}
        _getLaptopItem={_getPhoneItem}
        _getCardArr={_getCardArr}
      />
      <Promo />
      <Modal chosenPhone={phone} />
      <Card />
    </div>
  );
};

App.propTypes = {
  phone: PropTypes.object,
  users: PropTypes.array,
  laptops: PropTypes.array,
  cardArr: PropTypes.array,
  _getPhoneItem: PropTypes.func,
  _getCardArr: PropTypes.func
};

export default App;
