import React from 'react';
import PropTypes from 'prop-types';
import LaptopItem from './LaptopItem';

const Laptop = ({ laptops, _getLaptopItem, _getCardArr }) => {
  return (
    <section
      id='laptop'
      className='container-fluid pt-5 pb-5 bg-light text-dark'
    >
      <h1 className='text-center'>BEST LAPTOP</h1>
      <div className='row'>
        {laptops.map((laptop, index) => (
          <LaptopItem
            key={index}
            laptop={laptop}
            _getLaptopItem={_getLaptopItem}
            _getCardArr={_getCardArr}
          />
        ))}
      </div>
    </section>
  );
};

Laptop.propTypes = {
  Laptop: PropTypes.array,
  _getPhoneItem: PropTypes.func,
  _getCardArr: PropTypes.func
};

export default Laptop;
