import React, { useState } from 'react';
import PropTypes from 'prop-types';

const LaptopItem = ({ laptop, _getLaptopItem, _getCardArr }) => {
  const [showDesc, setShowDesc] = useState(false);

  const { name, img, desc } = laptop;
  const _showDesc = e => {
    e.preventDefault();
    setShowDesc(!showDesc);
  };

  const _onClick = e => {
    e.preventDefault();
    _getCardArr(laptop);
  };

  return (
    <div className='col-xs-12 col-sm-6 col-md-6 col-lg-3 col-lg-3'>
      <div className='container'>
        <div className='card bg-light' style={{ width: 300 }}>
          <img
            className='card-img-top'
            src={img}
            alt=''
            style={{ maxWidth: '100%', height: 250 }}
          />
          <div className='card-body text-center'>
            <h4 className='card-title text-center'>{name}</h4>
            {showDesc && <p className='card-text'>{desc}</p>}
            <a href='/' className='btn btn-primary' onClick={_showDesc}>
              Description
            </a>
            <a
              href='/'
              className='btn btn-dark'
              data-toggle='modal'
              data-target='#exampleModalScrollable'
              onClick={() => _getLaptopItem(laptop)}
            >
              Detail
            </a>
            <a
              href='/'
              className='btn btn-danger'
              onClick={_onClick.bind(this)}
            >
              Cart
            </a>
          </div>
        </div>
      </div>
    </div>
  );
};

LaptopItem.propTypes = {
  laptop: PropTypes.object.isRequired,
  _getPhoneItem: PropTypes.func,
  _getCardArr: PropTypes.func
};

export default LaptopItem;
