import React, { useState } from 'react';
import PropTypes from 'prop-types';

const PhoneItem = ({ _getPhoneItem, phone, _getCardArr }) => {
  const [showDesc, setShowDesc] = useState(false);
  const { name, desc, img } = phone;

  const _showDesc = e => {
    e.preventDefault();
    _getPhoneItem(phone);
    setShowDesc(!showDesc);
  };

  const _onClick = e => {
    e.preventDefault();
    _getCardArr(phone);
  };
  return (
    <div className='col-xs-12 col-sm-6 col-md-6 col-lg-3 col-lg-3'>
      <div className='container'>
        <div className='card bg-light' style={{ width: '300px' }}>
          <img
            src={img}
            alt=''
            className='card-img-top'
            style={{ maxWidth: '100%', height: 250 }}
          />
          <div className='card-body text-center'>
            <h4 className='card-title text-center'>{name}</h4>
            {showDesc && <p className='card-text'>{desc}</p>}
            <a href='/' className='btn btn-dark' onClick={_showDesc}>
              description
            </a>
            <a
              href='/'
              className='btn btn-primary'
              data-toggle='modal'
              data-target='#exampleModalScrollable'
              onClick={() => _getPhoneItem(phone)}
            >
              Detail
            </a>
            <a
              href='\'
              className='btn btn-danger'
              onClick={_onClick.bind(this)}
            >
              Cart
            </a>
          </div>
        </div>
      </div>
    </div>
  );
};

PhoneItem.propTypes = {
  phone: PropTypes.object.isRequired,
  showDesc: PropTypes.bool,
  _getPhoneItem: PropTypes.func.isRequired,
  _getCardArr: PropTypes.func.isRequired
};

export default PhoneItem;
